@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root{
    --color-bg: #1f1f38;
    --color-bg-variant: #02030a; 
    --color-primary: rgb(68, 167, 206);
    --color-primary-variant: rgba(77,181,255,0.4);
    --color-light: rgba(255, 255, 255, 0.774);

    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
}

html{
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display: none;
}

body {
    font-family: 'Poppins', sans-serif;
    background: var(var(--color-bg));
    color: white;
    line-height: 1.7;
    background-image: url(../src/assets/bacround.jpg);
}

.containter{
    width: var(--container-width-lg);
    margin: 0 auto
}


h2,
h3,
h4,
h5{
    font-weight: 500;
}


h4{
    font-size: larger;
}
h3{
    font-size: 1.5rem;
}

h1{
    font-size: 2.5rem ;
    font-weight: 500;
    color: var(--color-primary);
    text-align: center;
}

h2{
    font-size: 2.5rem;
}

section{
    margin-top: 8rem;
}

section > h2,
section > h5,
section > h1 
{
    text-align: center;
    color: var(--color-light);
}

section > h2{
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light{
    color: var(--color-light);
}

a { 
    color: var(--color-primary);
    transition: var(--transition);
}

.btn {
    width: 20%;
    height: 120%;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2 rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

btn:hover{
    background: white;
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary{ 
    background: var(--color-primary);
    color: var(--color-bg);
}

img{
    display: block;
    width: 100%;
    object-fit: cover;
}

/* ========= Media Queries (Medium Devices) =========== */

@media screen and (max-width: 1024px) {
    .containter {
        width: var(--container-width-md);
    }

    section {
        margin-top: 6rem;
    }
        
}

@media screen and (max-width: 600px) {
    .containter {
        width: var(--container-width-sm);
    }

    section > h2 {
        margin-top: 6rem;
    }
        
}